.movie{
    height: calc(100vh - 64px); // 64px es el tamaño de nuestro menú
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    
    &__dark{
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        background-color: black;
        opacity: 0.7;
    }

    .ant-row{
        height: 100%;
        display: flex;
        align-items: center;
    }

    &__poster{
        height: 70%;
        margin-right: 15px;

        div{
            height: 100%;
            background-repeat: no-repeat;
            background-position: center;
            background-size: contain;
        }
    }

    &__info{
        height: 70%;
        color: white;

        &-header{
            display: flex;
            align-items: center;
            justify-content: space-between;

            h1{
                color: white;
                margin: 0;
                font-size: 48px;

                span{
                    font-size: 20px;
                    margin-left: 10px;
                    opacity: 0.6;
                }
            }

            button{
                height: 100%;
                background-color: transparent;
                color: white;
                border: 0;
                font-size: 25px;

                &:hover, &:focus, &:active{
                    background-color: transparent;
                    color: #1890FF;
                }
            }
        }

        &-content{
            margin-top: 50px;
            margin-bottom: 500px !important;

            h3{
                color: white;
                font-weight: bold;
                font-size: 24px;
            }

            h4{
                color: white;
                font-weight: bold;
                font-size: 18px;
            }

            &-extended{
                display: flex;
                
                >div{
                width: 100%;
                margin-right: 100px;
                }
            }
        }
    }

    
}